import { j as json } from "../../../../../chunks/index.js";
async function GET({ locals: { supabase } }) {
  const { data: ents, error } = await supabase.from("entity").select();
  const { data: props, propError } = await supabase.from("property").select();
  const { data: psets, psetError } = await supabase.from("pset").select();
  for (const row of ents) {
    if (row.psets) {
      const psetData = {};
      for (const [pset_id, props_id] of Object.entries(row.psets)) {
        const psetName = psets.filter((x) => x.id == pset_id)[0].psetName;
        const thisProps = props.filter((x) => props_id.includes(x.id));
        psetData[psetName] = thisProps;
      }
      row.psets = psetData;
    }
  }
  ents.sort((a, b) => {
    a.test = a.key.replace("null", "");
    b.test = b.key.replace("null", "");
    if (a.test < b.test) {
      return -1;
    } else if (a.test > b.test) {
      return 1;
    }
    return 0;
  });
  return json(ents);
}
export {
  GET
};
